/* CSS Document */
body,ul,li,p,h1,h2,h3,h4,h5,h6,ol,dl,dd,dt{ margin:0; padding:0}
body{ line-height:1.5em; color:#666;font-family: ;}
ul{ list-style:none}
img{ border:none}
a{ text-decoration:none; color:#666;outline: none;}
a:hover{ text-decoration:none;}
a:focus{ outline: none}
s,i,em{font-style:normal;text-decoration:none;}
.fl{ float:left}
.fr{ float:right}
a:hover img {
	filter: alpha(opacity=80);
	opacity: 0.8;
	-webkit-transition: all .2s linear;
	transition: all .2s linear;
}
.clearfix:after{
   content:".";/**/
   display:block;/**/
   height:0;/**/
   clear:both;/**/
   visibility:hidden;/**/
  }
.clearfix{zoom:1;/*IE67*/}
/* for Chrome */
::-webkit-scrollbar {
    display: none;
}
/* header */
header{
    width: 90%;
    font-size: 1.5rem;
    padding: 5% 5% 3%;
    color: #363636;
    overflow: hidden;
    -webkit-box-shadow: 0 8px 14px -1px #fafafa;
            box-shadow: 0 8px 14px -1px #fafafa;
    background: #fff;
}
header .header-search img{
    width: 20px;
}
/* footer */
footer{
    width: 100%;
    position: fixed;
    bottom: 0;
    background: #fff;
    border-top: 1px #ddd solid;
}
footer li{
    width: 7.5%;
